.home {
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-light);
  position: relative;
  overflow: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding: 20px;
  margin: 0 auto;
}

.home-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  gap: 2rem;
}

.title-container {
  min-height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.home-title {
  font-size: clamp(2rem, 5vw, 4rem);
  text-transform: uppercase;
  font-weight: 900;
  text-shadow: 3px 3px 6px rgba(0,0,0,0.7);
  color: var(--primary-accent);
  letter-spacing: 3px;
  text-align: center;
  line-height: 1.2;
}

.subtitle-container {
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.home-subtitle {
  font-size: clamp(1rem, 3vw, 1.8rem);
  font-weight: 600;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.6);
  color: var(--text-light);
  text-align: center;
  line-height: 1.4;
}

.button-container {
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.btn {
  display: inline-block;
  padding: clamp(0.8rem, 2vw, 1rem) clamp(1.5rem, 4vw, 2.5rem);
  background-color: orangered;
  color: var(--text-light);
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.3s ease;
  font-size: clamp(0.9rem, 2.5vw, 1.2rem);
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  z-index: 1;
  text-align: center;
  white-space: normal;
  max-width: 90%;
  hyphens: auto;
  word-wrap: break-word;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: all 0.4s;
  z-index: -1;
}

.btn:hover::before {
  left: 100%;
}

.btn:hover::after {
  opacity: 1;
}

.btn::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--secondary-accent);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -2;
}

.btn:hover {
  color: var(--text-light);
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(255, 61, 0, 0.5);
}

.btn span {
  position: relative;
  z-index: 2;
}

@media (max-width: 768px) {
  .home-content {
    min-height: 70vh;
  }

  .btn {
    font-size: clamp(0.8rem, 2vw, 1rem);
    padding: 0.8rem 1.5rem;
    max-width: 100%;
  }
}